/* qr-code-window */

.qr-code-window {
  /* background-color: black; */
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.6);
  /* display: none; */
  transition: var(--window-transition);
  /* opacity: 0.8; */
}

.qr-code-pic {
  height: 10rem;
}

.qr-code-message-content {
  display: flex;
  flex-direction: column;
  gap: 2.3rem;
  /* justify-content: center; */
  align-items: center;
  padding-top: 4rem;
}

.qr-code-window-content {
  width: 47.125rem;
  padding: 2.8rem;
  background-color: black;
  border: 2px solid var(--light-gray-styles);
  border-radius: 10px;
  transition: var(--window-transition);
  position: relative;
}

.close-btn-frame {
  height: 1.6rem;
  width: 1.6rem;
  /* background-color: var(--gray); */
  border: 2px solid white;
  background-color: transparent;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.close-btn {
  border: none;
  background-color: transparent;
  /* float: right; */
  position: absolute;
  top: 2.8rem;
  right: 2.8rem;
}

.close-btn-frame:hover .close-btn-icon {
  fill: var(--bright-red);
}

.close-btn-frame:hover {
  cursor: pointer;
}

.close-btn-frame:hover {
  border-color: var(--bright-red);
}

.hidden-window .qr-code-window-content {
  transform: scale(0.9);
  visibility: hidden;
}

.hidden-window.qr-code-window {
  opacity: 0.9;
  visibility: hidden;
  /* pointer-events: auto; */
}
